    readonly reloadStateChange: EventEmitter<boolean>;
    /**
     * 10.0.1新增
     *
     * 设置是否使用路由来控制菜单对应内容显示/隐藏，默认使用路由。
     */
    routable: boolean;
    /**
     * 是否开启左侧菜单面板可折叠功能，默认开启。
     */
